@import '../../styles/common.css';

.box {
  align-items: center;
  border: 0.5px solid var(--brand-white, #fff);
  border-radius: 4px;
  box-sizing: border-box;
  justify-content: center;
  display: flex;
  height: 4rem;
  padding: var(--spacing-2, 0.5rem);
  width: 100%;
}

.box > img {
  max-height: 100%;
  max-width: 100%;
  object-fit: contain;
}

.container {
  width: 100%;
  height: 100vh;
  background-color: var(--brand-black, #000);
  color: var(--brand-white, #fff);
}

.background,
.foreground {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}

.background {
  height: 50%;
}

.content {
  max-width: 750px;
  display: flex;
  height: 100%;
  flex-direction: column;
  justify-content: end;
  margin: auto;
  padding: 10px;
  box-sizing: border-box;

  @media (max-width: 600px) {
    padding: 20px;
  }

  @media (min-width: 1600px) and (min-height: 950px) {
    max-width: 1000px;
  }
}

.content .copy {
  flex: 2;
  @media (max-width: 600px) and (max-height: 950px) {
    margin-top: -33%;
  }
}

.content .copyPadding {
  flex: 1;
}

.cyanSlash {
  color: var(--brand-cyan, #00ffff);
  display: inline-block;
  font-weight: bold;
  margin: 0 0.5rem;
}

.foreground {
  z-index: 2;
  height: 100%;
  width: 100%;
  overflow: hidden;
}

.grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 1rem;
  margin-top: 2rem;
}

.header1 {
  font-size: 4rem;
  margin-bottom: 1rem;
  @media (max-width: 450px) {
    font-size: 2.5rem;
    text-align: center;
    margin-bottom: 1rem;
    text-shadow: 0px 1px 0px var(--brand-black, #000);
  }
  @media (min-width: 1600px) and (min-height: 950px) {
    font-size: 5rem;
  }
}

.header2 {
  font-size: 1.5rem;
  @media (max-width: 450px) {
    font-size: 0.75rem;
    font-weight: bold;
    line-height: 1.5;
  }
  @media (min-width: 1600px) and (min-height: 950px) {
    font-size: 5rem;
  }
}

@media (max-width: 400px) {
  .resourceLinks li.hideOnMobile {
    display: none;
  }
}

.listBullet {
  font-size: 66%;
  margin-right: 0.25rem;
}

.navigation {
  display: flex;
  flex-direction: column;
}

.navigation > ul {
  flex: 1;
  margin-top: 1rem;
}

.navigation > ul a {
  display: flex;
  flex-direction: row;
  align-items: center;

  @media (max-width: 600px) and (max-height: 950px) {
    border: 1px solid var(--brand-black, #000);
    padding: 0.25rem 0.5rem;
    box-sizing: border-box;
    margin-bottom: 0.25rem;
    text-align: center;
  }
}

.purpleSlash {
  color: var(--brand-purple, #b19cd9);
  display: inline-block;
  font-weight: bold;
  margin: 0 0.5rem;
}

.resourceLinks {
  display: flex;
  flex-direction: row;
  gap: var(--spacing, 1rem);
  margin-top: calc(var(--spacing, 1rem) * 2);

  @media (max-width: 600px) {
    gap: 0;
  }
}

.resourceLinks ul {
  display: flex;
  height: 100%;
  @media (max-width: 600px) {
    align-self: center;
    justify-self: center;
  }
}

.resourceLinks li {
  @media (max-width: 600px) {
    display: flex;
    flex: 1;
  }
}

.resourceLinks a {
  @media (max-width: 600px) {
    display: flex;
    flex: 1;
    height: 100%;
    justify-content: center;
    width: 100%;
    font-weight: bold;
  }
  @media (max-width: 400px) {
    font-size: 90%;
  }
}

.responsiveMessage {
  line-height: 1.5;
  display: none;
  border: 2px solid var(--brand-white, #fff);
  border-radius: 0.3rem;
  padding: 0.25rem 0.5rem;
  font-size: 80%;
  box-sizing: border-box;
  margin-top: 0.5rem;

  @media only screen and (max-device-width: 640px) {
    display: block;
  }
}

.responsiveMessage ul {
  box-sizing: border-box;

  padding-left: 0.5rem;
}

.responsiveMessage li {
  margin-left: 0.5rem;
  list-style: circle;
}

.slash {
  display: inline-block;
  font-weight: bold;
  @media (max-width: 600px) {
    display: none;
  }
}
